.movie-card 
{
  background: #ffffff;
  box-shadow: 18px rgba(0, 0, 0, 0.1);
  width: 100%;
  height: 85%;
  max-width: 315px;
  margin: 2em;
  border-radius: 10px;
  display: inline-block;
}
.movie-header 
{
  padding: 0;
  margin: 0;
  height: 367px;
  width: 100%;
  display: flex;
}
.movie-card:hover
{
  -webkit-transform: scale(1.03);
  transform: scale(1.03);
  box-shadow: 0px 10px 25px rgba(0, 0, 0, 0.08);        
}
.movie-content 
{
  padding: 18px 18px 24px 18px;
  margin: 0;
}
  
.movie-content-header, .movie-info 
{
  display: table;
  width: 100%;
}
  
.movie-title 
{
  font-size: 24px;
  margin: 0;
  display: table-cell;
}
.movie-info 
{
  margin-top: 1em;
  display: flex;
  align-items: flex-end;
  flex-wrap: wrap;
}  
.info-section 
{
  display: table-cell;
  text-transform: uppercase;
  text-align: left;
}  
.info-section label 
{
  display: block;
  color: rgba(0, 0, 0, 0.5);
  margin-bottom: .5em;
  font-size: 10px;
  color: white;
}  
.info-section span 
{
  font-weight: 700;
  font-size: 11px;
}

img 
{
  width: 100%;
  position: relative;
}
.overlay > * 
{
  transform: translateY(20px);
  transition: transform 0.25s;
}

.overlay:hover > * 
{
  transform: translateY(0); 
}
.overlay
{
  position: absolute;
  width: 310px;
  height: 72%;
  background: rgba(0, 0, 0, 0.6);
  color: white;
  opacity: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 7px;
  transition: opacity 0.25s;
}
.overlay:hover 
{
   opacity: 1;
}
.container
{
  display: grid;
  grid-template-columns: auto auto auto;
  gap: 10px 3px;
  grid-auto-flow: row;
  margin-top: 100px;  
}
.form 
{
  position: relative
}
.form span 
{
  position: absolute;
  right: 17px;
  top: 13px;
  padding: 2px;
  border-left: 1px solid #d1d5db
}
.left-pan 
{
  padding-left: 7px
}
.form-input 
{
  height: 55px;
  text-indent: 33px;
  border-radius: 10px
}
.toto
{
  margin-top: 20px;
}